import React from 'react';
import Slider from 'react-slick';

const HomeOurTeam = () => {
   const settings = {
      dots: true,
      infinite: false,
      speed: 300,
      arrows: false,
      slidesToShow: 4,
      slidesToScroll: 1,
      responsive: [
         {
            breakpoint: 1200,
            settings: {
               slidesToShow: 4,
               slidesToScroll: 1,
               infinite: true,
               dots: true
            }
         },
         {
            breakpoint: 600,
            settings: {
               slidesToShow: 2,
               slidesToScroll: 1
            }
         },
         {
            breakpoint: 480,
            settings: {
               slidesToShow: 2,
               slidesToScroll: 1
            }

         }
      ]
   };

   return (
      <>
         <section className="team-area pt-115">
            <div className="container">
               <div className="row">
                  <Slider className='testimonials-activation team__area' {...settings}>
                     <div>
                        <img src="img/brand/brand-1.jpg" alt='brand-1' />
                     </div>
                     <div>
                        <img src="img/brand/brand-2.jpg" alt='brand-2' />
                     </div>
                     <div>
                        <img src="img/brand/brand-3.jpg" alt='brand-3' />
                     </div>
                     <div>
                        <img src="img/brand/brand-4.jpg" alt='brand-4' />
                     </div>
                     <div>
                        <img src="img/brand/brand-1.jpg" alt='brand-1' />
                     </div>
                     <div>
                        <img src="img/brand/brand-2.jpg" alt='brand-2' />
                     </div>
                     <div>
                        <img src="img/brand/brand-3.jpg" alt='brand-3' />
                     </div>
                     <div>
                        <img src="img/brand/brand-4.jpg" alt='brand-4' />
                     </div>
                  </Slider>
               </div>
            </div>
         </section>
      </>
   );
};

export default HomeOurTeam;