import React from 'react';
import AboutSingleCounter from '../../../components/AboutSingleCounter/AboutSingleCounter';

const AboutCounter = () => {
   return (
      <>
         <section className="counter-wraper pt-120 pb-90 gray-bg">
            <div className="container">
               <div className="row">
                  <AboutSingleCounter icon="1" counter="1" suffix={'M'} title="Satisfed Patients" />
                  <AboutSingleCounter icon="2" counter="5" suffix={'K'} title="Expert Doctors" />
                  <AboutSingleCounter icon="3" counter="10" suffix={'K'} title="Happy Pharmacists & Distributors" />
               </div>
            </div>
         </section>
      </>
   );
};

export default AboutCounter;