import { BrowserRouter, Route, Routes } from 'react-router-dom';
import ScrollTop from './components/ScrollTop';
import AllContext from './context/AllContext';
import AboutUs from './pages/AboutUs/AboutUs';
import Contact from './pages/Contact/Contact';
import Home from './pages/Home/Home/Home';
import ServicesDetails from './pages/Services/ServicesDetails/ServicesDetails';
import Manufacturing from './pages/Services/Manufacturing/Manufacturing';
import ServicesOne from './pages/Services/ServicesOne/ServicesOne';
import VisionPage from './pages/Services/VisionPage/VisionPage';
import ShopDetails from './pages/ShopDetails/ShopDetails/ShopDetails';
import ShopPage from './pages/ShopPage/ShopPage/ShopPage';
import './App.css';
import NotFound from './pages/NotFound/NotFound';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import CareerPage from './pages/Career/Career';

function App() {
  return (
    <>
      <AllContext>
        <BrowserRouter>
          <ScrollTop />
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/services" element={<ServicesOne />} />
            <Route path="/vision-page" element={<VisionPage />} />
            <Route path="/commitment" element={<ServicesDetails />} />
            <Route path="/manufacturing" element={<Manufacturing />} />
            <Route path="/shop/:cat?" element={<ShopPage />} />
            <Route path="/shop-details/:id" element={<ShopDetails />} />
            <Route path="/about" element={<AboutUs />} />
            <Route path="/career" element={<CareerPage />} />
            <Route path="/contact" element={<Contact />} />
            <Route path="/notMatch" element={<NotFound />} />
            <Route path="*" element={<NotFound />} />
          </Routes>
        </BrowserRouter>
      </AllContext>
    </>
  );
}

export default App;
