import React from 'react';
import { useParams } from 'react-router-dom';
import SingleShop from '../../../../components/SingleShop/SingleShop';
import { Product } from '../../../../config';

const ShopBanner = () => {

   const { cat } = useParams()
   const ProductList = Product.filter((list) => {
      if (cat !== undefined) {
         return list.catrgory.toLowerCase() === cat.toLowerCase()
      } else {
         return list
      }
   })
   return (
      <>
         <section className="shop-banner-area pt-120 pb-120">
            <div className="container">
               <div className="row">
                  <div className="col-12">
                     <div className="tab-content" id="myTabContent">
                        <div className="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">
                           <div className="row">
                              {
                                 ProductList.map((item) => (
                                    <SingleShop name={item.catrgory} key={item.name} title={item.name} {...item} />
                                 ))
                              }
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      </>
   );
};

export default ShopBanner;