import React from 'react';
import CommonPageHeader from '../../../components/CommonPageHeader/CommonPageHeader';
import Footer from '../../../components/Shared/Footer';
import HomeHeader from '../../Home/Home/HomeHeader/HomeHeader';

const ServicesDetails = () => {
    return (
        <>
            <HomeHeader />
            <CommonPageHeader title="Commitment" subtitle="Commitment" />
            <section className="about-area pt-120 pb-90">
                <div className="container">
                    <div className="row ">
                        <div className="col-12">
                            <div className="facalty-text mb-50">
                                <p>We are committed to our customers, business associates as well as employees and work to earn
                                    their trust every day. We are passionate in our pursuit of excellence; driven by the unyielding desire to
                                    execute on our mission and to achieve our corporate vision. This commitment extends to every one of our
                                    operations, allowing us to achieve the highest possible standards in our processes, products and services.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </>
    );
};

export default ServicesDetails;