import React from 'react';

const HomeAboutArea = () => {
   return (
      <>
         <section className="about-area pt-120 pb-90">
            <div className="container">
               <div className="row">
                  <div className="col-xl-6 col-lg-5 about_left">
                     <div className="about-left-side pos-rel mb-30">
                        <div className="about-front-img">
                           <img src="img/page/about-us.jpg" alt="" />
                        </div>
                        <div className="about-shape">
                           <img src="img/about/about-shape.png" alt="" />
                        </div>
                     </div>
                  </div>
                  <div className="col-xl-6 col-lg-7">
                     <div className="about-right-side pt-55 mb-30">
                        <div className="about-title mb-20">
                           <h5>About Us</h5>
                           <h1 className='font-size-50'>Welcome to Hindkind Pharmaceuticals</h1>
                           <h5>A company that is kind to every human being and trulydedicated to Healthcare!</h5>
                        </div>
                        <div className="about-text">
                           <p>At Hindkind Pharmaceuticals, our focus is on developing medicines that are of highest
                              quality as well as affordable to the patients. We have been active in the discovery and
                              development, manufacturing and marketing of novel healthcare solutions. We are at
                              the forefront of providing quality pharmaceuticals that improve the quality of lives
                              and help people live longer and better lives.</p>
                           <p>Our products bring significant benefit to the patients as per our motto “Kind to every
                              Human being” with quality and product value.</p>
                           <p>
                              <b>We manufacture and market pharmaceutical formulations covering a broad
                                 spectrum of chronic and acute therapies. It includes generics, branded generics,
                                 specialty, complex or difficult to make technology-intensive products and overthe-counter (OTC) products.</b></p>
                           <p>
                              <b>At Hindkind, we only believes in quality of pharmaceutical products and our all
                                 manufacturing facilities follows</b> the international quality standards like WHO, GMP,
                              GLP, CMP as well as ISO certification.
                           </p>
                           <p>
                              Hindkind Pharmaceuticals also plays a pioneering role in personalized healthcare and
                              is providing the products which are tailored to the needs of specific patient groups.
                           </p>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      </>
   );
};

export default HomeAboutArea;