import React from 'react';
import { Link } from 'react-router-dom';

const SingleShop = ({ name, title, ...other }) => {
    const image = other.image[0].name
    const url = title.replace(new RegExp(" ", "gm"), "-")
    return (
        <>
            <div className="col-lg-4 col-md-6">
                <div className="product mb-40">
                    <div className="product__img">
                        <Link to={`/shop-details/${url}`}><img src={`/img/shop/${image}.jpg`} alt="" /></Link>
                    </div>
                    <div className="product__content text-center pt-30">
                        <span className="pro-cat">{name}</span>
                        <h4 className="pro-title"><Link to={`/shop-details/${url}`}>{title}</Link></h4>
                    </div>
                </div>
            </div>
        </>
    );
};

export default SingleShop;