import React from 'react';

const AboutArea = () => {
   return (
      <>
         <section className="about-area pt-120 pb-90">
            <div className="container">
               <div className="row">
                  <div className="col-xl-6 col-lg-5 about_left">
                     <div className="about-left-side pos-rel mb-30">
                        <div className="about-front-img">
                           <img src="img/about/visionpage.jpg" alt="visionpage" />
                        </div>
                        <div className="about-shape">
                           <img src="img/about/about-shape.png" alt="" />
                        </div>
                     </div>
                  </div>
                  <div className="col-xl-6 col-lg-7">
                     <div className="about-right-side pt-55 mb-30">
                        <div className="about-title mb-20">
                           <h5>About Us</h5>
                           <h1>Vision</h1>
                        </div>
                        <div className="about-text">
                           <div className="ser-fea-list fix">
                              <h3>Personal care</h3>
                              <ul>
                                 <li className='d-flex align-items-baseline'><i className="fas fa-check"></i>to become the leader in Indian Pharmaceutical
                                    Industry by providing high quality and affordable medicines.</li>
                                 <li className='d-flex align-items-baseline'><i className="fas fa-check"></i>Reaching to People And Touching their Lives
                                    Globally As A Leading Provider Of Valued Medicines.</li>
                                 <li className='d-flex align-items-baseline'><i className="fas fa-check"></i>To be respected and admired by patients, physicians
                                    and pharmacists.</li>
                                 <li className='d-flex align-items-baseline'><i className="fas fa-check"></i>To stick with our aim and motto “ kind to every
                                    human being” with product quality and price in every situation.</li>
                                 <li><i className="fas fa-check"></i>To achieve sustained sales and profit growth.</li>
                              </ul>
                           </div>
                        </div>
                        <div className="about-text mt-15">
                           <div className="ser-fea-list fix">
                              <h3>Personal care</h3>
                              <p>
                                 We are committed to our customers, business
                                 associates as well as employees and work to earn their trust
                                 every day. We are passionate in our pursuit of excellence;
                                 driven by the unyielding desire to execute on our mission and
                                 to achieve our corporate vision. This commitment extends to
                                 every one of our operations, allowing us to achieve the highest
                                 possible standards in our processes, products and services
                              </p>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      </>
   );
};

export default AboutArea;